import $ from 'jquery';
import { findDOMNode } from 'react-dom';
import './App.css';
import { createBrowserRouter, RouterProvider, Navigate, createHashRouter, Link } from 'react-router-dom'
import Home from './Component/Home/Home';
import Movies from './Component/Movies/Movies';
import MasterLayout from './Component/MasterLayout/MasterLayout';
import NotFound from './Component/NotFound/NotFound';
import About from './Component/About/About';
import People from './Component/People/People';

import Tv from './Component/Tv/Tv';
import { useState, useEffect } from 'react';
import jwt_decode from 'jwt-decode'
import MovieDetiles from './Component/moviesDetiles/MovieDetiles';
import TvDetails from './Component/TvDetails/TvDetails';
import BookNow from './Component/BookNow/BookNow';
import Latest from './Component/Latest/Latest';
import Offers from './Component/Offers/Offers';
import Derma from './Component/Derma/Derma';
import Studio from './Component/Studio/Studio';
import Call from './Component/Call/Call';
import Causes from './Component/Causes/Causes';
import Table from './Component/Table/Table';
import Gum from './Component/Gum/Gum';
import Implant from './Component/Implant/Implant';
import Khamis from './Component/Khamis/Khamis';
import Talal from './Component/Talal/Talal';
import Maher from './Component/Maher/Maher';


function App() {
  let [user, setUser] = useState(null)

  useEffect(() => {
    if (localStorage.getItem("token") != null) {
      saveUserData()
    }
  }, [])


  function saveUserData() {
    let token = localStorage.getItem("token")
    let data = jwt_decode(token)
    console.log(data);
    setUser(data)
  }
  function LogOut() {
    localStorage.removeItem("token")
    setUser(null)
   
  }
  function ProtectedRouter(props) {
    console.log(props);
    if (localStorage.getItem("token") == null) {
      // login
     
    }
    else {
      // path
      return props.children
    }
  }
  let Routers = createHashRouter([
    {
      path: "/", element: <MasterLayout />, children: [
        { index: true, element: <ProtectedRouter><Home /></ProtectedRouter> },
        { path: "home", element: <ProtectedRouter><Home /></ProtectedRouter> },
        { path: "about", element: <ProtectedRouter><About /></ProtectedRouter> },
        { path: "movies", element: <ProtectedRouter><Movies /></ProtectedRouter> },
        { path: "moviedetils/:id", element: <ProtectedRouter><MovieDetiles /></ProtectedRouter> },
        { path: "people", element: <ProtectedRouter><People /></ProtectedRouter> },
        { path: "tv", element: <ProtectedRouter><Tv /></ProtectedRouter> },
        { path: "book", element: <ProtectedRouter><BookNow /></ProtectedRouter> },
        { path: "latest", element: <ProtectedRouter><Latest /></ProtectedRouter> },
        { path: "offers", element: <ProtectedRouter><Offers /></ProtectedRouter> },
        { path: "derma", element: <ProtectedRouter><Derma /></ProtectedRouter> },
        { path: "studio", element: <ProtectedRouter><Studio /></ProtectedRouter> },
        { path: "call", element: <ProtectedRouter><Call /></ProtectedRouter> },
        { path: "causes", element: <ProtectedRouter><Causes /></ProtectedRouter> },
        { path: "table", element: <ProtectedRouter><Table /></ProtectedRouter> },
        { path: "gum", element: <ProtectedRouter><Gum /></ProtectedRouter> },
        { path: "implant", element: <ProtectedRouter><Implant /></ProtectedRouter> },
        { path: "khamis", element: <ProtectedRouter><Khamis /></ProtectedRouter> },
        { path: "talal", element: <ProtectedRouter><Talal /></ProtectedRouter> },
        { path: "maher", element: <ProtectedRouter><Maher /></ProtectedRouter> },
        { path: "tvdetails/:id", element: <ProtectedRouter><TvDetails /></ProtectedRouter> },
        

        { path: "*", element: <NotFound /> },
      ]
    }

  ])
  return (
    <>


      <RouterProvider router={Routers} />
     
    </>
  );
}

export default App;
