import React from "react";
import { Link, NavLink } from "react-router-dom";

export default function Navbar() {
  

  return (
    <>
    
      <nav className="navbar navbar-expand-lg navbar-light bg-transparent">
        <div className="container-fluid">
          <Link className="navbar-brand px-5 prety" to="/">
          Joele
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
           
              <ul className="navbar-nav me-auto mb-2 mb-lg-0 px-5 abvn">
                <li className="nav-item  ">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to="home"
                  >
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to="about"
                  >
                    About 
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to="movies"
                  >
                    Services
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to="people"
                  >
                    our doctors
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to="tv"
                  >
                    medical blog
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to="studio"
                  >
                    Joele's studio
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className={({ isActive }) =>
                      isActive ? "nav-link active" : "nav-link"
                    }
                    to="call"
                  >
                    call us
                  </NavLink>
                </li>
               <Link to="latest">
               <button className=" Live  spelt">
                  Latest offers
                </button>
               </Link>
               
                <Link to="book">
                <button className="btn btn-info mx-4 p-3 spkeee ">
                  Book Now
                </button>
                </Link>
              
              </ul>
            
          </div>
        </div>
      </nav>
    </>
  );
}
